<template>
  <div>
    <b-row>
      <b-col
        cols="12"
        md="2"
        order="2"
        order-md="1"
        class="p-1 p-md-0 m-0 mt-0"
      >
        <b-badge
          class="mb-2"
          :variant="isConnected? 'success': 'danger'"
        >
          <feather-icon
            :icon="isConnected? 'ZapIcon': 'ZapOffIcon'"
            class="mr-25"
          />
          <span>{{ isConnected? 'Conectado': 'Desconectado' }}</span>
        </b-badge>
        <side-bar-teams-units
          ref="refSideBarTeamsUnits"
          :form="form"
          :clean-filter="cleanFilter"
          :on-teams-change="onTeamsChange"
          :on-units-change="onUnitsChange"
          :on-option-change="onOptionChange"
        />
      </b-col>
      <b-col
        cols="12"
        md="10"
        order="1"
        order-md="2"
        class="p-0 m-0"
      >
        <mapa-tracking
          ref="mapa"
          class="contenido-principal"
          :trackings="trackings"
          :init-map="initMap"
        />
      </b-col>
    </b-row>
    <side-bar-details
      :show-sidebar="showSidebar"
      :details="details"
    />
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { useResponsiveAppLeftSidebarVisibility } from '@core/comp-functions/ui/app'
import {
  BCol,
  BRow,
  BBadge,
} from 'bootstrap-vue'
import MapaTracking from './components/MapaTracking.vue'
import SideBarTeamsUnits from './components/SideBarTeamsUnits.vue'
import SideBarDetails from './components/SideBarDetails.vue'
import UseTrackingsWS from './composables/useTrackingsWS'

export default {
  components: {
    BCol,
    BRow,
    BBadge,
    MapaTracking,
    SideBarTeamsUnits,
    SideBarDetails,
  },
  created() {
    this.$store.commit('verticalMenu/UPDATE_VERTICAL_MENU_COLLAPSED', true)
  },
  destroyed() {
    this.$store.commit('verticalMenu/UPDATE_VERTICAL_MENU_COLLAPSED', false)
  },
  setup() {
    const { mqShallShowLeftSidebar } = useResponsiveAppLeftSidebarVisibility()
    const searchQuery = ref()
    const perfectScrollbarSettings = {
      maxScrollbarLength: 150,
    }
    const mapa = ref()
    const refSideBarTeamsUnits = ref()

    const {
      isConnected,
      showSidebar,
      details,
      form,
      trackings,
      cleanFilter,
      onTeamsChange,
      onUnitsChange,
      onOptionChange,
      initMap,
    } = UseTrackingsWS(mapa, refSideBarTeamsUnits)

    return {
      isConnected,
      searchQuery,
      perfectScrollbarSettings,
      mqShallShowLeftSidebar,
      form,
      showSidebar,
      details,
      trackings,
      mapa,
      refSideBarTeamsUnits,
      cleanFilter,
      onTeamsChange,
      onUnitsChange,
      onOptionChange,
      initMap,
    }
  },
}
</script>
<style>
.contenido-principal {
  height: 300px !important;
}
/* Para pantallas medianas en adelante (md y lg) */
@media (min-width: 576px) {
  .contenido-principal {
    height: 78vh !important; /* Altura total del viewport */
  }
}
</style>
