<template>
  <l-map
    ref="mapLeaflet"
    :zoom="zoom"
    :center="center"
    class="l-map"
    @ready="initMap()"
  >
    <v-icondefault />
    <l-tile-layer
      :url="url"
    />
  </l-map>
</template>
<script>
import {
  LMap,
  LTileLayer,
  LIconDefault,
} from 'vue2-leaflet'
import 'leaflet/dist/leaflet.css'
import { ref } from '@vue/composition-api'

export default {
  components: {
    LMap,
    LTileLayer,
    'v-icondefault': LIconDefault,
  },
  props: {
    trackings: {
      required: true,
      type: Array,
    },
    initMap: {
      required: true,
      type: Function,
    },
  },
  methods: {
    click: e => { console.log(e) },
    ready: e => { console.log(e) },
  },
  setup() {
    const mapLeaflet = ref()
    const show = ref(false)
    const style = ref('normal.day.grey')
    const apiKey = ref('q9wf_iQvH_xa-W8fVJJGKV2M0g1WvPDQEVZh489fY-c')
    const url = ref(`https://2.base.maps.ls.hereapi.com/maptile/2.1/maptile/newest/${style.value}/{z}/{x}/{y}/512/png8?apiKey=${apiKey.value}&ppi=320`)
    // const url = ref('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png')
    const zoom = ref(5)
    const center = ref([19.433026740813453, -99.1332049])

    return {
      show,
      style,
      apiKey,
      url,
      zoom,
      center,
      mapLeaflet,
    }
  },
}
</script>
  <style lang="scss">
  .vue2leaflet-map {
    &.leaflet-container {
      height: 350px;
    }
  }

  /* Animación de Pulso de los Círculos en el Centro */
  @keyframes pulse {
      0% { r: 56; }
      50% { r: 64; }
      100% { r: 56; }
  }

  .marker {
      animation: bounce 1s infinite; /* Duración incrementada a 2 segundos */
  }

  #id_103 {
      animation: blink 1s infinite;
  }

  #id_111,
  #id_113 {
      animation: pulse 1.5s infinite;
  }
  .l-map {
    z-index: 1 !important;
  }
  </style>
