import { Manager } from 'socket.io-client'
import L from 'leaflet'
import _ from 'lodash'

import { onMounted, reactive, ref } from '@vue/composition-api'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import { useToast } from 'vue-toastification/composition'

import TrackingService from '@/services/TrackingService'
import UnitService from '@/services/UnitService'
import 'leaflet-rotatedmarker'
import {
  truckGreen,
  truckGreenInverse,
  truckRed,
  truckRedInverse,
} from '../icons'

const UseTrackingsWS = (refMapa, refSideBarTeamsUnits) => {
  const STATUS_ACTIVE = 1

  const toast = useToast()
  const URL_SOCKET = `${process.env.VUE_APP_BASE_URL}/socket.io/socket.io.js`
  const manager = new Manager(URL_SOCKET)
  const socket = manager.socket('/trackings')
  const isConnected = ref(false)
  const isLoading = ref(false)
  const showSidebar = reactive({
    show: false,
  })
  const units = ref([])
  const trackings = ref([])
  const unitsToFilter = ref([])
  const layer = ref()

  const form = reactive({
    unitsSelected: [],
    teamsSelected: [],
  })
  const markersList = ref([])
  const detailsEmpty = {
    plate: '',
    brand: {
      name: '',
    },
    model: {
      name: '',
    },
    driver: {
      photo: '',
      name: '',
      lastName: '',
      cellphone: '',
    },
    devices: [],
    lastTracking: {
      address: '',
    },
  }
  const details = reactive(detailsEmpty)

  const resolveIconMarker = (statusTrackingId, angle) => {
    // angleInDegrees > 0 && angleInDegrees <= 180
    // const tolerance = 10
    const direction = angle >= -100 && angle <= 100 ? 'right' : 'left'
    console.log({ angle })

    let icon = null
    if (statusTrackingId === STATUS_ACTIVE) {
      icon = direction === 'right' ? truckGreen : truckGreenInverse
    } else {
      icon = direction === '' ? truckRed : truckRedInverse
    }
    return L.divIcon({
      className: 'leaflet-data-marker',
      html: icon.replace('#', '%23'),
      iconAnchor: [22, 28],
      iconSize: [36, 42],
      popupAnchor: [0, -30],
    })
  }

  const updateIconMarker = unit => {
    markersList.value = markersList.value.map(m => {
      if (unit.id === m.unitId) {
        m.setIcon(resolveIconMarker(unit.statusTracking.id, 0))
      }
      return m
    })
  }

  const addListeners = () => {
    socket.on('connect', () => {
      isConnected.value = true
    })

    socket.on('update-status-tracking', unit => {
      refSideBarTeamsUnits.value.updateStatusTracking(unit.id, unit.statusTracking)
      updateIconMarker(unit)
    })

    socket.on('disconnect', () => {
      isConnected.value = false
    })
  }

  const getUnitDetailsByIdUnit = async (unitId, trackingId) => {
    try {
      Object.assign(details, detailsEmpty)
      const response = await UnitService.getWithLastTrackingById(unitId, trackingId)
      Object.assign(details, response.data)
      showSidebar.show = true
    } catch (error) {
      Object.assign(details, detailsEmpty)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al consultar Detalles de la unidad',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const lerp = (start, end, t) => start + (end - start) * t

  const animateMarkerMovement = (marker, startCoords, endCoords, duration = 1000) => {
    const startTime = performance.now()
    const animate = currentTime => {
      const elapsedTime = currentTime - startTime
      const progress = Math.min(elapsedTime / duration, 1) // Progreso de 0 a 1
      // Interpolamos latitud y longitud
      const currentLat = lerp(startCoords.lat, endCoords.lat, progress)
      const currentLng = lerp(startCoords.lng, endCoords.lng, progress)
      marker.setLatLng([currentLat, currentLng])
      // Continuar la animación hasta completar
      if (progress < 1) {
        requestAnimationFrame(animate)
      }
    }
    requestAnimationFrame(animate)
  }

  const calculateDirectionAngle = (startCoords, endCoords) => {
    const dy = endCoords.lat - startCoords.lat
    const dx = endCoords.lng - startCoords.lng
    const angle = Math.atan2(dy, dx) * (180 / Math.PI) // Convertir de radianes a grados
    return angle
  }

  const fitBounds = () => {
    const coordinates = markersList.value.map(marker => marker.getLatLng())
    if (!coordinates.length) return
    const bounds = L.latLngBounds(coordinates)
    refMapa.value.$refs.mapLeaflet.fitBounds(bounds)
  }

  const addEventTracking = unitId => {
    socket.on(`tracking-${unitId}`, tracking => {
      let marker = markersList.value.find(m => m.unitId === unitId)
      const [lat, lng] = tracking.location.coordinates
      // const coordinates = L.latLng(lng, lat)
      const newCoordinates = L.latLng(lng, lat)
      refSideBarTeamsUnits.value.updateStatusTracking(unitId, tracking.unit.statusTracking)
      if (marker) {
        marker.trackingId = tracking.id
        // Si el marcador ya existe, interpolamos el movimiento
        const currentCoordinates = marker.getLatLng()
        // marker.setLatLng(coordinates)
        // Animación suave entre la posición actual y la nueva
        const angle = calculateDirectionAngle(currentCoordinates, newCoordinates)
        animateMarkerMovement(marker, currentCoordinates, newCoordinates)
        marker.setIcon(resolveIconMarker(STATUS_ACTIVE, angle))
        /* if ((angle > 45 && angle < 135) || (angle < -45 && angle > -135)) {
          marker.setRotationAngle(angle)
        } else {
          marker.setRotationAngle(0)// Sin rotación horizontal
        } */
      } else {
        const meIcon = resolveIconMarker(STATUS_ACTIVE, 0)
        marker = L.marker(newCoordinates, { icon: meIcon, title: tracking.unit.plate, rotationAngle: 0 }).addTo(layer.value)
        marker.bindPopup(`<b>${tracking.unit.plate}</b>`).openPopup()
        marker.unitId = unitId
        marker.trackingId = tracking.id
        markersList.value.push(marker)

        marker.on('click', () => {
          getUnitDetailsByIdUnit(marker.unitId, marker.trackingId)
        })
      }
      // centrar mapa
      // fitBounds()
    })
  }

  const removeEventTracking = unitId => {
    socket.off(`tracking-${unitId}`)
  }

  const trackingToMarker = tracking => {
    const meIcon = resolveIconMarker(tracking.status_tracking_id, 0)
    const {
      location,
      plate,
      unit_id: unitId,
      tracking_id: trackingId,
    } = tracking
    const [lng, lat] = location.geometry.coordinates
    const coordinates = L.latLng(lat, lng)
    const marker = L.marker(coordinates, { icon: meIcon, title: plate, rotationAngle: 0 }).addTo(layer.value)
    marker.bindPopup(`<b>${plate}</b>`).openPopup()
    marker.unitId = unitId
    marker.trackingId = trackingId
    markersList.value.push(marker)

    // addEventTracking(unitId)
    marker.on('click', () => {
      getUnitDetailsByIdUnit(marker.unitId, marker.trackingId)
    })

    return {
      ...tracking,
      location: coordinates,
      marker,
    }
  }

  const removeMarker = unitId => {
    const markerSelected = Array.from(markersList.value).find(m => m.unitId === unitId)
    if (markerSelected) {
      layer.value.removeLayer(markerSelected)
      markersList.value = markersList.value.filter(m => m.unitId !== markerSelected.unitId)
      // removeEventTracking(unitId)
    }
  }

  const getTrackingsByUnitsIds = async unistIds => {
    try {
      const response = await TrackingService.fetchTrackingsByUnitsIds(unistIds)
      trackings.value = response.data.map(trackingToMarker)
      // centrar mapa
      fitBounds()
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al consultar trackings',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const cleanFilter = () => {
    form.unitsSelected = []
    form.teamsSelected = []
    markersList.value = []
    layer.value.clearLayers()
  }

  const reload = _.debounce(() => {
    getTrackingsByUnitsIds(unitsToFilter.value)
    unitsToFilter.value = []
  }, 1000)

  const onTeamsChange = team => {
    const isSelected = form.teamsSelected.includes(team.id)
    const unitsOfTeam = team.units.filter(u => u.status === true).map(unit => unit.id)
    if (isSelected) {
      form.unitsSelected = [...form.unitsSelected, ...unitsOfTeam]
      unitsToFilter.value = [...unitsToFilter.value, ...unitsOfTeam]
      unitsOfTeam.forEach(addEventTracking)
      reload()
    } else {
      form.unitsSelected = form.unitsSelected.filter(unitId => !unitsOfTeam.includes(unitId))
      unitsOfTeam.map(removeMarker)
      unitsOfTeam.map(removeEventTracking)
    }
  }
  const onUnitsChange = unitId => {
    const selected = form.unitsSelected.includes(unitId)
    if (selected) {
      unitsToFilter.value.push(unitId)
      addEventTracking(unitId)
      reload()
    } else {
      removeMarker(unitId)
      removeEventTracking(unitId)
    }
  }

  const onOptionChange = value => {
    if (value === 'teams') {
      cleanFilter()
    }
  }

  const initMap = () => {
    layer.value = L.layerGroup().addTo(refMapa.value.$refs.mapLeaflet.mapObject)
  }

  onMounted(() => {
    addListeners()
  })

  return {
    isLoading,
    isConnected,
    units,
    trackings,
    getTrackingsByUnitsIds,
    form,
    showSidebar,
    details,
    cleanFilter,
    onTeamsChange,
    onUnitsChange,
    onOptionChange,
    initMap,
    addListeners,
  }
}

export default UseTrackingsWS
